$brd-10 : 10px;
$brd-30 : 30px;
$p10: 10px;
$zero: 0 !important;
$white: white;
$cb: black;
$blk0: #ffffff00;
$blk40: rgba(26, 47, 62, 0.486);
$p100: 100%;
$flex: flex;
$row-r: row-reverse;
$brd1: 1px;
$sld: solid;
$mc: #022374;
$cd: #00132f;
$cs: #fab52c;
$rm07: 0.7rem;

/**Edicion bloque primary menu*****/
.custom-flex{
    height: 5vh;
}
.custom-flex div {
    display: flex;
    width: $p100;
    align-items: center;
    justify-content: flex-end;
    height: $p100;
}
 
/**edicion search*/
.busqueda form#search-block-form {
    display: $flex;
    flex-direction: $row-r;
}

.busqueda form#search-block-form {
    display: $flex;
    flex-direction: $row-r;
    border: $brd1 $sld $mc;
    border-radius: $rm07;
    height: $p100;
}
.busqueda input#edit-keys--2, .busqueda input#edit-keys {
    border: none;
    background: $blk0;
    border-left-width: $brd1;
    border-left-color: $cb;
    border-left-style: $sld;
    padding-left: $p10;
}
.busqueda input#edit-keys--2:focus-visible, .busqueda input#edit-keys:focus-visible {
    outline: none;
}
.busqueda input#edit-submit, .busqueda input#edit-submit--3 {
    background: $blk0;
    border: none;
}
/**edicion botones primary nav*/
.primary-nav-boton a.btn {
    background-color: #022374;
    min-width: 150px;
    margin-left: 2px;
    border: none;
    color: #00132f;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.7rem;
}
.primary-nav-boton a.btn-main-color.btn-color.btn i {
    margin-left: 4px;
}
.primary-nav-boton a.btn-main-color.btn-color.btn{
    background-color: $mc;
    color: $white;
}
.primary-nav-boton a.btn-secundary.btn{
    background-color: $cs;
}


/**edicion area de titulo de cada pagina**/
.back-title{
    background-image: url('../images/computer-g4b10d37c4_1920.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y:center;
    height: 30vh;
    border-radius: $brd-30 ;
    color: $white;
    padding: $zero;
    margin: $zero;
}
.blur{
    background-color: $blk40;
    width: $p100;
    height: $p100;
    border-radius: $brd-30;
}
.idiomas ul.links {
    background: black;
}

/**site logo al 100% **/
a.navbar-brand {
    width: $p100;
}
a.navbar-brand img {
    width: $p100;
}